<template>
  <div class="page" v-show="load">
    <div class="page__content">
      <div class="page__header">
        <breadcrumbs />
        <h1 class="page__title">
          {{ startup.title }}
        </h1>
      </div>
      <div class="art-startup">
        <div class="art-startup__header">
          <div class="art-startup__block-first">
            <div class="art-startup__genre" v-if="getCategory">
              {{ getCategory }}
            </div>
          </div>
          <div class="art-startup__block-second">
            <div class="art-startup__bookmark">
              <svg width="20" height="20">
                <use href="@/assets/img/sprite.svg#icon-bookmark"></use>
              </svg>
              В избранном
            </div>
          </div>
        </div>
      </div>
      <div class="art-startup__body">
        <div class="art-startup__image" v-if="startup.title_picture.url">
          <img :src="startup.title_picture.url" alt="" />
        </div>

        <div class="art-startup__description">
          <div class="art-startup__col-left">
            <h2 class="art-startup__title">Описание</h2>
            <div v-html="startup.body"></div>
          </div>
          <div class="art-startup__col-right">
            <div class="art-startup__rating">
              <h2 class="art-startup__title">Рейтинг</h2>
              <div class="art-startup__rating-body">
                <star-rating :read-only="true" :star-size="24" :rating="4" />
                <div class="art-startup__voting">417 голосов</div>
              </div>
            </div>

            <div class="art-startup__members">
              <span class="art-startup__members-name">Автор: </span>
              <div class="art-startup__members-tags">
                <span class="art-startup__members-tag">
                  {{ startup.author.full_name }}
                </span>
              </div>
              <span class="art-startup__members-name">Участники: </span>
              <div class="art-startup__members-tags">
                <span class="art-startup__members-tag">
                  Иванова Александра
                </span>
                <span class="art-startup__members-tag">
                  Чакраборти Елизавета
                </span>
              </div>
            </div>
          </div>
        </div>

        <comments :comments="startup.comments" @add-comment="addNewComment" />
      </div>
    </div>
  </div>
</template>

<script>
import StarRating from "vue-star-rating";
import axios from "axios";
import Comments from "../components/Comments";
import qs from "qs";

export default {
  name: "ArtStartupPage",
  components: { Comments, StarRating },
  data() {
    return {
      startup: {
        author: {
          full_name: ""
        },
        title_picture: {
          url: ""
        }
      },
      load: false
    };
  },
  mounted() {
    this.loadData();
  },

  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/startup/view`);
      url.searchParams.set("id", this.$route.params.id);

      await axios
        .get(url)
        .then(response => {
          this.startup = {
            ...response.data
          };
          this.load = true;
          load.hide();
        })
        .catch(error => {
          if (error.response.status === 404) {
            this.$toasted.global.error("Арт-стартап не найден");
            this.$router.push("/art-startups");
          } else {
            this.$toasted.global.error(error.response.data.message);
          }
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          load.hide();
        });
    },
    async addNewComment(comment) {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/startup-comment/create`,
          qs.stringify({ startup_id: this.startup.id, body: comment.body })
        )
        .then(result => {
          this.startup.comments.push(result.data);
        })
        .catch(error => {
          console.log(error.request);
        });
    }
  },
  computed: {
    getCategory() {
      return this.startup.categories
        ? this.startup.categories.map(item => item.title).join(" ● ")
        : "";
    },
    getTitlePicture() {
      return this.startup.title_picture.url || "";
    }
  }
};
</script>
