<template>
  <div class="reviews">
    <div class="reviews__header">
      <h2 class="reviews__title">Отзывы и оценки</h2>
      <a
        @click="gotoNewComment"
        class="reviews__btn button-secondary"
        v-if="isAuthenticated"
        >комментировать</a
      >
    </div>

    <div
      class="reviews__list"
      v-for="(comment, index) in comments"
      :key="index"
    >
      <div class="reviews__img">
        <img :src="comment.author.avatar.url" alt="" />
      </div>
      <div class="reviews__wrap">
        <div class="reviews__name">{{ comment.author.full_name }}</div>
        <div class="reviews__date">{{ comment.created_at | formatDate }}</div>
        <div class="reviews__comment">{{ comment.body }}</div>
      </div>
    </div>

    <!--        New comment-->
    <div class="reviews__list" ref="new-comment" v-if="isAuthenticated">
      <div class="reviews__img">
        <img :src="currentUser.avatar.url" alt="" />
      </div>
      <div class="reviews__wrap">
        <div class="reviews__name">{{ currentUser.full_name }}</div>
        <textarea
          class="reviews__input"
          placeholder="Написать отзыв"
          v-model="newComment.body"
        >
        </textarea>
        <button class="reviews__btn-comment button-primary" @click="addComment">
          комментировать
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "../mixin/mixin";
import { mapGetters } from "vuex";

export default {
  name: "Comments",
  props: {
    comments: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      newComment: {
        body: ""
      },
      currentUser: {
        avatar: {
          url: ""
        },
        full_name: ""
      }
    };
  },
  mixins: [mixin],
  methods: {
    addComment() {
      this.$emit("add-comment", this.newComment);
      this.newComment.body = "";
    },
    gotoNewComment() {
      window.scroll({
        top: this.$refs["new-comment"].offsetTop,
        behavior: "smooth"
      });
    }
  },
  mounted() {
    this.currentUser = this.$store.getters["user/getUser"];
  },
  computed: {
    ...mapGetters("auth", ["isAuthenticated"])
  }
};
</script>

<style scoped></style>
